import React from 'react';
import Chart from 'react-apexcharts'

const colors = [
    '#008FFB',
    '#00E396',
    '#FEB019',
    '#FF4560',
    '#775DD0',
    '#546E7A',
    '#26a69a',
    '#D10CE8'
]

function ChartBar(props){
    const data ={
        series: [{
            name: props.label,
            data: props.data
        }],
        options: {
            chart: {
                height: 350,
                type: 'bar',
                events: {
                    click: function(chart, w, e) {
                        // console.log(chart, w, e)
                    }
                }
            },
            colors: colors,
            plotOptions: {
                bar: {
                    columnWidth: '45%',
                    distributed: true
                }
            },
            dataLabels: {
                enabled: true
            },
            legend: {
                show: false
            },
            xaxis: {
                categories: props.labels,
                labels: {
                    style: {
                        colors: colors,
                        fontSize: '12px'
                    }
                }
            }
        },
    };

    return (
        <Chart options={data.options} series={data.series} type="bar" height={`${props.width}px`} />
    );
}

export default ChartBar;