import React, { Component } from 'react';
import { Input, InputGroup, Icon, SelectPicker, Alert } from 'rsuite';
import ModalSearch from './elements/ModalSearch';
import fData from '../assets/ModalSearchFData';
import URL_API from '../assets/conf';
import SELECT_OPTIONS from '../assets/selectOpctions';
import '../styles/header.css';

class Header extends Component{
    state = {
        table: "",
        idSearch: "",
        data: {},
        cargar: false
    };
    
    handleUpdateTableSearch = (e) => {
        this.setState({
            table: e
        });
    }

    handleUpdateIdSearch = (e) => {
        this.setState({
            idSearch: e
        });
    }

    handleCloseModal = () =>{
        this.setState({
            cargar: false,
        }); 
    }

    handleCheckId = async () =>{
        let {table, idSearch} = this.state;

        if(table !== "")
        {
            if(idSearch !== "")
            {
                try {
                    table = table === "1" ? 4 : 1;
                    let resp = await fetch(`${URL_API}/checkid/${table}+${idSearch}`);
                    let data = await resp.json();

                    data = data.msg === "successful" ? data.value : "error";

                    if (data === 1){
                        table = table === 4 ? "nutres" : "madres";

                        let resp = await fetch(`${URL_API}/${table}/${idSearch}`);
                        let data = await resp.json();

                        if (data.msg === "successful"){
                            table = table === "nutres" ? 1 : 2;
                            let itemsData = fData(table, data.data[0]);

                            this.setState({
                                data: {
                                    id: data.data[0].id,
                                    title: `${table === 1 ? "Proveedor": "Madre"} ${data.data[0].id}`,
                                    date: data.data[0].date,
                                    items: itemsData,
                                },
                                cargar: true,
                                show: true,
                            });  
                        }
                    } else Alert.warning('Id no encontrado')

                } catch (error) {
                    Alert.error('Ha ocurrido un error')
                }
            } else this.props.updateOption(0, table);
        } else Alert.warning('Seleccione una opcin')
    }

    render(){
        return (
            <div className="header-container">
                <div className="header-title">
                    <span>Dashboard | {this.props.title}</span>
                </div>

                <div className="header-search">
                    <SelectPicker
                        searchable={false}
                        size="md"
                        data={SELECT_OPTIONS}
                        appearance="subtle"
                        placeholder="Seleccione una opcin"
                        onSelect={this.handleUpdateTableSearch}
                        style={{
                            width: "35%", 
                            "background":"#bdd3ca",
                            borderRadius: 5
                        }}
                    />
                    <InputGroup 
                        size="md" inside 
                        style={{ width: "55%"}}
                        >
                        <Input placeholder="Id" onPressEnter={this.handleCheckId} onChange={this.handleUpdateIdSearch}/>
                        <InputGroup.Button onClick={this.handleCheckId}>
                            <Icon icon="search" />
                        </InputGroup.Button>
                    </InputGroup>
                </div>

                <div className="header-other">
                    <div className="modal-container">
                        <ModalSearch data={this.state.data} show={this.state.cargar} close={this.handleCloseModal}/>
                    </div>
                </div>
            </div>
        );
    }
}

export default Header;