import React from 'react';
import {Tooltip, Whisper} from 'rsuite';
import {
    BrowserRouter as Router, Switch, Route, Link
} from "react-router-dom";

import 'rsuite/dist/styles/rsuite-default.min.css';
import './styles/main.css';
import Logo from './assets/icons/insp.png';
import NAV_OPTIONS from './assets/navOptions';
import NotFound from './pages/notFound';
import MainContainer from './pages/mainContainer';


function App(){
    return(
        <Router>
            <div className="container">
                <nav className="nav">
                    <div className="nav-logo">
                        <img src={Logo} alt="Logo INSP"/>
                    </div>
                    <div className="nav_links_container">
                        {
                            NAV_OPTIONS.map(e =>{
                                return <Whisper key={e.id} placement="auto" trigger="hover" speaker={<Tooltip>{e.text}</Tooltip>}>
                                    <Link to={e.to} className="nav_links">
                                        <button><img src={e.icon} alt={e.alt}/></button>
                                    </Link>
                                </Whisper>
                            })
                        }
                    </div>
                </nav>
                <main className="main">
                    <Switch>
                        {
                            NAV_OPTIONS.map(e =>{
                                return <Route key={e.id} exact path={e.to}>
                                        <MainContainer 
                                            navOption={e.id}
                                            title={e.textHeader}
                                        />
                                </Route>
                            })
                        }
                        <Route component={NotFound}></Route>
                    </Switch>
                </main>
            </div>
        </Router>
    );
}

export default App;
