import React, { Component, Fragment } from 'react';
import Panel1 from './Panel1';
import { Row } from 'rsuite';
import URL_API from '../../assets/conf';
import Element from '../elements/Element';
import ChartLine from '../charts/LineCreatedOn';

const ChartDates = (props) => {
    return(
        <Row className="box-element-container">
            {  props.cargar ?
                <Element style={{"background": "#d9f2e5"}} title={props.title} md={24} fill={1}>
                    <center style={{color: 'gray', fontSize: '12px'}}>{props.total} Registros</center>
                    <ChartLine data1={props.data}/>
                </Element>
                : <Element md={24} />
            }
        </Row>
    );
}

class Home extends Component{
    state ={
        cargar: false,
        total: 0, tota2: 0, tota3: 0, tota4: 0,
        dates: {}, dates2: {}, dates3: {}, dates4: {},
        error: false
    }

    fetchDates = async () =>{
        try {
            let resp = await fetch(`${URL_API}/home/dates/1/2020-08-01`);
            let resp2 = await fetch(`${URL_API}/home/dates/2/2020-08-01`);
            let resp3 = await fetch(`${URL_API}/home/dates/3/2020-08-01`);
            let resp4 = await fetch(`${URL_API}/home/dates/4/2020-08-01`);
            let data = await resp.json();
            let data2 = await resp2.json();
            let data3 = await resp3.json();
            let data4 = await resp4.json();

            if (data.msg === "successful" && data2.msg === "successful")
            {
                return {
                    1: data.data,
                    2: data2.data,
                    3: data3.data,
                    4: data4.data
                };
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDates();

        if (data[1] !== 'error' && data[2] !== 'error' && data[3] !== 'error' && data[4] !== 'error')
        {
            this.setState({
                cargar: true,
                total: data[1].length,
                total2: data[2].length,
                total3: data[3].length,
                total4: data[4].length,
                dates: {
                    name: data[1].name,
                    value: data[1].value
                },
                dates2: {
                    name: data[2].name,
                    value: data[2].value
                },
                dates3: {
                    name: data[3].name,
                    value: data[3].value
                },
                dates4: {
                    name: data[4].name,
                    value: data[4].value
                }
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }

    render(){
        return(
            <Fragment>
                <section className="main_panel1">
                    <section className="panel_contect">
                        <Panel1 />
                    </section>
                </section>
                <section className="main_panel2">
                    <section className="panel_contect">
                        <ChartDates cargar={this.state.cargar} 
                        title="Actividad Nutres c." 
                        data={this.state.dates} 
                        total={this.state.total}
                        />
                        
                        <ChartDates cargar={this.state.cargar} 
                        title="Actividad Madres c." 
                        data={this.state.dates2} 
                        total={this.state.total2}
                        />

                        <ChartDates cargar={this.state.cargar} 
                        title="Actividad Proveedores" 
                        data={this.state.dates3} 
                        total={this.state.total3}
                        /> 

                        <ChartDates cargar={this.state.cargar} 
                        title="Actividad Cuidadoras" 
                        data={this.state.dates4} 
                        total={this.state.total4}
                        /> 
                    </section>
                </section>
            </Fragment>
        );
    }
}

export default Home;