import React, { Fragment } from 'react';
import { Placeholder, Col } from 'rsuite';

const { Paragraph } = Placeholder;

function ElementText(props) {
    return (
        <Col xs={24}  md={props.md}>
            <div style={props.style} className="box-element-content box-element-content-response">
                { props.fill ? 
                    <center>
                        <span className={props.color ? "title-box-element color-white" : "title-box-element"}>{props.title}</span>
                        {props.children}
                    </center>
                    :<Fragment>
                        <Paragraph rows={props.rows} style={{marginBottom: "10px"}} active/>
                    </Fragment>
                }
            </div>
        </Col>
    );
}

export default ElementText;