import React, { Component, Fragment } from 'react';
import URL_API from '../../assets/conf';
import { Radio, RadioGroup, SelectPicker, Row } from 'rsuite';
import Element from '../elements/Element';
import RowTop from '../elements/RowTop';
import SELECT_OPTIONS from './optionsSections';
import SecGeneral from './SecGeneral';
import SecUnidadSalud from './SecUnidadSalud';
import SecPromocion from './SecPromocion';
import SecEstrategia from './SecEstrategia';
import Error from '../elements/error';

const SECTION = [SecGeneral, SecUnidadSalud, SecPromocion, SecEstrategia];

const styles = {
    radioGroupLabel: {
        padding: '8px 2px 8px 10px',
        display: 'inline-block',
        verticalAlign: 'middle'
    }
};

class Panel1 extends Component{
    state ={
        group: "0",
        section: "0",
        length: 0,
        cargar: false,
        dates: {},
        error: false
    }

    setGroup = e =>{
        this.setState({
            group: e
        });
    }

    setSectionCharts = e =>{
        this.setState({
            section: e
        });
    }

    fetchDates = async () =>{
        try {
            let resp = await fetch(`${URL_API}/home/dates/1/2020-08-01`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                return data.data;
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDates();

        if (data !== 'error')
        {
            this.setState({
                length: data.length,
                dates: {
                    name: data.name,
                    value: data.value
                },
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }

    render(){
        if (this.state.error){
            return <Error />
        }
        
        let Section = SECTION[this.state.section];

        return(
            <Fragment>
                {/* Actividades generales */}
                { this.state.cargar ?
                    <RowTop
                        length={this.state.length}
                        createdOn={this.state.dates}
                    />
                    : <Row className="box-element-container">
                        <Element md={8} /><Element md={8} /><Element md={8} />
                    </Row>
                }

                <div className="nutres__panel1_bar-tools">
                    <RadioGroup onChange={this.setGroup} name="group_list" inline appearance="picker" defaultValue="0">
                        <span style={styles.radioGroupLabel}>Grupo: </span>
                        <Radio value="0">Todos</Radio>
                        <Radio value="1">Control</Radio>
                        <Radio value="2">Intervencin</Radio>
                    </RadioGroup>
                    <SelectPicker
                        searchable={false}
                        size="md"
                        data={SELECT_OPTIONS}
                        appearance="subtle"
                        placeholder="Seleccione una seccin"
                        onSelect={this.setSectionCharts}
                        defaultValue="0"
                        style={{
                            width: "30%", 
                            "background":"#bdd3ca",
                            borderRadius: 5
                        }}
                    />
                </div>
                <Section group={this.state.group}/>
            </Fragment>
        );
    }
}

export default Panel1;