import React, { Component } from 'react';
import '../styles/placeholder.css';
import '../styles/charts.css';
import '../styles/sections.css';
import Home from './home/Home';
import Madres from './madres/Madres';
import Cuidadoras from './cuidadoras/Cuidadoras';
import Proveedores from './proveedores/Proveedores';
import Nutres from './nutres/Nutres';
import Search from './search/Search';

const MODULES = [Search, Home, Nutres, Madres, Proveedores, Cuidadoras];

class MainPanel extends Component{
    render(){
        let Panel = MODULES[this.props.option];
        let { option, search } = this.props;
        
        return(
            <Panel search={option === 0 ? search : null}/>
        );
    }
}

export default MainPanel;