import React, { Component, Fragment } from 'react';
import Panel1 from './panel1';
import Panel2Nutres from '../nutres/Panel2';
import Panel2Madres from '../madres/Panel2';

const PANEL2 = {
    1: Panel2Nutres,
    2: Panel2Madres,
}

class Search extends Component{
    render(){
        let { search } = this.props;
        let Panel2 = PANEL2[search.table]

        return(
            <Fragment>
                <section className="main_panel1">
                    <section className="panel_contect">
                        <Panel1 search={search}/>
                    </section>
                </section>
                <section className="main_panel2">
                    <section className="panel_contect">
                        <Panel2 />
                    </section>
                </section>
            </Fragment>
        );
    }
}

export default Search;