import React, { Component, Fragment } from 'react';
import URL_API from '../../assets/conf';
import { Grid, Row } from 'rsuite';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';
import Error from '../elements/error';

class SecPromocion extends Component{
    state ={
        _group_: this.props.group,
        cargar: false,
        error: false,
        atendioProspera: {},
        impartenTalleresComunitarios: {},
        eventosPromocion: {},
        impartenTemasComunitariosG: {},
        imparteUTalleres: {},
        capacitacion: {},
        cuantosTalleres: {},
        materialDidactico: {},
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/nutres/seccion/promocion/${this.props.group}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                
                return {
                    atendioProspera: await data.data.map(e =>{  return parseInt(e.atendioProspera)  }),
                    impartenTalleresComunitarios: await data.data.map(e =>{  return parseInt(e.impartenTalleresComunitarios)  }),
                    eventosPromocion: await data.data.map(e =>{  return parseInt(e.eventosPromocion)  }),
                    impartenTemasComunitariosG: await data.data.map(e =>{  return parseInt(e.impartenTemasComunitariosG)  }),
                    imparteUTalleres: await data.imparteUTalleres.map(e =>{  return parseInt(e)  }),
                    capacitacion: await data.data2.map(e =>{  return parseInt(e.capacitacion)  }),
                    cuantosTalleres: await data.data2.map(e =>{  return parseInt(e.cuantosTalleres)  }),
                    materialDidactico: await data.data2.map(e =>{  return parseInt(e.materialDidactico)  }),
                }
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDataAll();

        if (data !== 'error')
        {
            this.setState({
                atendioProspera: {
                    si: await data.atendioProspera.filter(e => e === 1).length,
                    no: await data.atendioProspera.filter(e => e === 2).length,
                    _99_: await data.atendioProspera.filter(e => e === 99).length,
                },
                impartenTalleresComunitarios: {
                    _1_: await data.impartenTalleresComunitarios.filter(e => e === 1).length,
                    _2_: await data.impartenTalleresComunitarios.filter(e => e === 2).length,
                    _3_: await data.impartenTalleresComunitarios.filter(e => e === 3).length,
                    _0_: await data.impartenTalleresComunitarios.filter(e => e === 0).length,
                    _99_: await data.impartenTalleresComunitarios.filter(e => e === 99).length,
                    nan: await data.impartenTalleresComunitarios.filter(e => isNaN(e)).length,
                },
                eventosPromocion: {
                    _1_: await data.eventosPromocion.filter(e => e === 1).length,
                    _2_: await data.eventosPromocion.filter(e => e === 2).length,
                    _3_: await data.eventosPromocion.filter(e => e === 3).length,
                    _0_: await data.eventosPromocion.filter(e => e === 0).length,
                    _99_: await data.eventosPromocion.filter(e => e === 99).length,
                    nan: await data.eventosPromocion.filter(e => isNaN(e)).length,
                },
                impartenTemasComunitariosG: {
                    _1_: await data.impartenTemasComunitariosG.filter(e => e === 1).length,
                    _2_: await data.impartenTemasComunitariosG.filter(e => e === 2).length,
                    _3_: await data.impartenTemasComunitariosG.filter(e => e === 3).length,
                    _0_: await data.impartenTemasComunitariosG.filter(e => e === 0).length,
                    _8888_: await data.impartenTemasComunitariosG.filter(e => e === 8888).length,
                    nan: await data.impartenTemasComunitariosG.filter(e => isNaN(e)).length,
                },
                imparteUTalleres: {
                    si: await data.imparteUTalleres.filter(e => e === 1).length,
                    no: await data.imparteUTalleres.filter(e => e === 2).length,
                },
                cuantosTalleres: {
                    _1_: await data.cuantosTalleres.filter(e => e === 0).length,
                    _2_: await data.cuantosTalleres.filter(e => e >= 1 && e <= 2).length,
                    _3_: await data.cuantosTalleres.filter(e => e >= 3 && e <= 4).length,
                    _4_: await data.cuantosTalleres.filter(e => e >= 5 && e <= 6).length,
                    _5_: await data.cuantosTalleres.filter(e => e >= 7 && e !== 99).length,
                    _99_: await data.cuantosTalleres.filter(e => e === 99).length,
                },
                capacitacion: {
                    si: await data.capacitacion.filter(e => e === 1).length,
                    no: await data.capacitacion.filter(e => e === 2).length,
                    _8888_: await data.capacitacion.filter(e => e === 8888).length,
                },
                materialDidactico: {
                    si: await data.materialDidactico.filter(e => e === 1).length,
                    no: await data.materialDidactico.filter(e => e === 2).length,
                    _8888_: await data.materialDidactico.filter(e => e === 8888).length,
                },
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }
    
    componentDidUpdate = async () =>{
        if (this.props.group !== this.state._group_)
        {
            this.setState({
                cargar: false,
                _group_: this.props.group
            });
            
            await this.handleGetData();
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }
        
        // if (!this.state.cargar){
        //     return <h1>Cargando...</h1>
        // }

        return (
            <Fragment>
                <Grid fluid style={{margin:0, padding:0}}>
                    {/* Graficas de Prospera y Talleres C. */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Atendi poblacin del programa prospera?" md={11} fill={1}>
                                <ChartPie
                                    width={200}
                                    data={[this.state.atendioProspera.si, this.state.atendioProspera.no, this.state.atendioProspera._99_]} 
                                    labels={["Si", "No", "No sabe"]}
                                />
                            </Element>
                            :<Element md={11}/>
                        }
                        { this.state.cargar ?
                            <Element title="Imparten talleres/platicas comunitarias sobre salud (Autocuidado/Embarazadas/Nios p.)?" md={13} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    data={[
                                        this.state.impartenTalleresComunitarios._1_, 
                                        this.state.impartenTalleresComunitarios._2_, 
                                        this.state.impartenTalleresComunitarios._3_, 
                                        this.state.impartenTalleresComunitarios._0_, 
                                        this.state.impartenTalleresComunitarios._99_, 
                                        this.state.impartenTalleresComunitarios.nan
                                    ]}
                                    labels={[
                                        "Si, se imparten talleres en la comunidad", 
                                        "Si, se imparten plticas en la comunidad", 
                                        "Si, se imparten plticas y talleres en la comunidad", 
                                        "No", 
                                        "No sabe", 
                                        "Sin resp."
                                    ]}
                                />
                            </Element>
                            : <Element md={13} />
                        }
                    </Row>

                    {/* Graficas de Prospera y Talleres C. */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Se tienen eventos/programas para el autocuidado de la salud?" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={250}
                                    data={[
                                        this.state.eventosPromocion._1_, 
                                        this.state.eventosPromocion._2_, 
                                        this.state.eventosPromocion._3_, 
                                        this.state.eventosPromocion._0_, 
                                        this.state.eventosPromocion._99_, 
                                        this.state.eventosPromocion.nan
                                    ]}
                                    labels={[
                                        "Si, se tienen programas propios de la US", 
                                        "Si, se tienen eventos propios de la US", 
                                        "Si, se tienen programas/eventos propios de la US", 
                                        "No", 
                                        "No sabe", 
                                        "Sin resp."
                                    ]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                        { this.state.cargar ?
                            <Element title="Imparten talleres comunitarios (temas en general)?" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={250}
                                    data={[
                                        this.state.impartenTemasComunitariosG._1_, 
                                        this.state.impartenTemasComunitariosG._2_, 
                                        this.state.impartenTemasComunitariosG._3_, 
                                        this.state.impartenTemasComunitariosG._0_, 
                                        this.state.impartenTemasComunitariosG._8888_, 
                                        this.state.impartenTemasComunitariosG.nan
                                    ]}
                                    labels={[
                                        "Si, se imparten talleres", 
                                        "Si, se imparten plticas", 
                                        "Si, se imparten plticas y talleres", 
                                        "No", 
                                        "No sabe", 
                                        "Sin resp."
                                    ]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas de imparten talleres y cuantos T. */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Usted imparte talleres?" md={11} fill={1}>
                                <ChartPie
                                    width={200}
                                    data={[this.state.imparteUTalleres.si, this.state.imparteUTalleres.no]} 
                                    labels={["Si", "No"]}
                                />
                            </Element>
                            :<Element md={11}/>
                        }
                        { this.state.cargar ?
                            <Element title="Cuntos talleres comunitarios (ltimos 3 meses)?" md={13} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    data={[
                                        this.state.cuantosTalleres._1_, 
                                        this.state.cuantosTalleres._2_, 
                                        this.state.cuantosTalleres._3_, 
                                        this.state.cuantosTalleres._4_, 
                                        this.state.cuantosTalleres._5_, 
                                        this.state.cuantosTalleres._99_
                                    ]}
                                    labels={[
                                        "Ninguno", 
                                        "1-2 Talleres", 
                                        "3-4 Talleres", 
                                        "5-6 Talleres", 
                                        "7+ Talleres", 
                                        "No sabe/No responde"
                                    ]}
                                />
                            </Element>
                            : <Element md={13} />
                        }
                    </Row>

                    {/* Graficas de capacitacion y material D. */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Ha recibido capacitacin?" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    data={[this.state.capacitacion.si, this.state.capacitacion.no, this.state.capacitacion._8888_]} 
                                    labels={["Si", "No", "No sabe"]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                        { this.state.cargar ?
                            <Element title="La US cuenta con material didctico?" md={12} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    data={[
                                        this.state.materialDidactico.si, 
                                        this.state.materialDidactico.no, 
                                        this.state.materialDidactico._8888_,
                                    ]}
                                    labels={["Si", "No", "No sabe"]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>
                </Grid>
            </Fragment>
        );
    }
}

export default SecPromocion;