import React, { Component, Fragment } from 'react';
import { Row } from 'rsuite';
import SecGeneral from './SecGeneral';
import URL_API from  '../../assets/conf';
import Element from '../elements/Element';
import RowTop from '../elements/RowTop';
import Error from '../elements/error';

class Panel1 extends Component{
    state ={
        length: 0,
        cargar: false,
        dates: {},
        error: false
    }

    fetchDates = async () =>{
        try {
            let resp = await fetch(`${URL_API}/home/dates/4/2020-08-01`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                return data.data;
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDates();

        if (data !== 'error')
        {
            this.setState({
                length: data.length,
                dates: {
                    name: data.name,
                    value: data.value
                },
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }

    render(){
        if (this.state.error){
            return <Error />
        }

        return(
            <Fragment>
                {/* Actividades generales */}
                { this.state.cargar ?
                    <RowTop
                        length={this.state.length}
                        createdOn={this.state.dates}
                    />
                    : <Row className="box-element-container">
                        <Element md={8} /><Element md={8} /><Element md={8} />
                    </Row>
                }
                <SecGeneral />
            </Fragment>
        );  
    }
}

export default Panel1;