import React, { Component } from 'react';
import { Row, Grid } from 'rsuite';
import URL_API from '../../assets/conf';
import ElementData from '../elements/ElementDataBox';
import Error from '../elements/error';

class Panel1 extends Component{
    state = {
        cargar: false,
        error: false,
        data: {}
    }

    fetchDates = async () =>{
        try {
            let resp = await fetch(`${URL_API}/home`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                return {
                    data: data.data,
                };
            }
        } catch (error) {
            return "error";
        }
    }
    
    handleGetData = async () =>{
        let data = await this.fetchDates();
        if (data !== 'error')
        {
            this.setState({
                data: data.data,
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }

    render(){
        if (this.state.error){
            return <Error />
        }

        return(
            <Grid fluid style={{margin:0, padding:0}}>
                <Row className="box-element-container">
                    <ElementData md={8} idx={0} cargar={this.state.cargar} data={this.state.data.nRemplazoCovid} of={this.state.data.nlength}/>
                    <ElementData md={8} idx={1} cargar={this.state.cargar} data={this.state.data.nEncargadoUS} of={this.state.data.nlength}/>
                    <ElementData md={8} idx={3} cargar={this.state.cargar} data={this.state.data.nHNutres} of={this.state.data.nlength}/>
                </Row>
                <Row className="box-element-container">
                    <ElementData md={12} idx={2} cargar={this.state.cargar} data={this.state.data.nAProspera} of={this.state.data.nlength}/>
                    <ElementData md={12} idx={7} cargar={this.state.cargar} data={this.state.data.mBeneficiarioINSABI} of={this.state.data.mlength}/>
                </Row>
                <Row className="box-element-container">
                    <ElementData md={8} idx={4} cargar={this.state.cargar} data={this.state.data.nEntendioPreguntas} of={this.state.data.nlength}/>
                    <ElementData md={8} idx={5} cargar={this.state.cargar} data={this.state.data.nInterezado} of={this.state.data.nlength}/>
                    <ElementData md={8} idx={6} cargar={this.state.cargar} data={this.state.data.nRespuestasConfiables} of={this.state.data.nlength}/>
                </Row>
                <Row className="box-element-container">
                    <ElementData md={8} idx={8}cargar={this.state.cargar} data={this.state.data.mEmbarazada} of={this.state.data.mlength}/>
                    <ElementData md={8} idx={10}cargar={this.state.cargar} data={this.state.data.mSexoHijoMujer} of={this.state.data.mlength}/>
                    <ElementData md={8} idx={9}cargar={this.state.cargar} data={this.state.data.mSexoHijoHombre} of={this.state.data.mlength}/>
                </Row>
            </Grid>
        );
    }
}

export default Panel1;