import React, { Component, Fragment } from 'react';
import { Grid, Row, Loader, Alert } from 'rsuite';
import ModalSearch from '../elements/ModalSearch';
import fData from '../../assets/ModalSearchFData';
import ElementTetx from '../elements/ElementTexts';
import URL_API from  '../../assets/conf';
import Item from './item';
import Error from '../elements/error';

const DICT = {
    1: {
        table: 'nutres',
        labels: ["Entidad", "Grupo", "Cargo", "Remplazo COVID-19"],
        labelsObj: ["entidad", "grupo", "cargo", "remplazo"]
    },
    2: {
        table: 'madres',
        labels: ["Entidad", "Grupo", "INSABI","Embarazada actualmente"],
        labelsObj: ["entidad", "grupo", "bInsabi", "embarazada"]
    }
}

class Panel1 extends Component{
    state = {
        table: this.props.search.table,
        data: {},
        cargar: false,
        error: false,
        modal:{
            show: false,
            data: {},
        }
    }

    fetchDataAll = async () => {
        let table = DICT[this.props.search.table].table;

        try {
            let resp = await fetch(`${URL_API}/${table}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                this.setState({
                    data: data.data,
                    cargar: true,
                });
            }
        } catch (error) {
            this.setState({
                error: true
            });
        }
    }

    async componentDidMount(){
        await this.fetchDataAll();
    }

    async componentDidUpdate(){
        if (this.state.table !== this.props.search.table){
            this.setState({
                table: this.props.search.table,
                cargar: false
            });

            await this.fetchDataAll();
        }
    }

    handleGetDataModal = async (id) =>{
        try {
            let table = DICT[this.props.search.table].table;
            let resp = await fetch(`${URL_API}/${table}/${id}`);
            let data = await resp.json();
    
            if (data.msg === "successful"){
                table = table === "nutres" ? 1 : 2;
                let itemsData = fData(table, data.data[0]);

                return{
                    id: data.data[0].id,
                    title: `${table === 1 ? "Proveedor": "Madre"} ${data.data[0].id}`,
                    date: data.data[0].date,
                    items: itemsData,
                }; 
            }
    
        } catch (error) {
            Alert.error('Ha ocurrido un error')
            return "error"
        }
    }

    handleOpenModal = async (id) =>{
        let data = await this.handleGetDataModal(id);
        
        if (data !== "error"){
            this.setState({
                modal: {
                    show: true,
                    data: data
                }
            });
        }
    }

    handleCloseModal = () =>{
        this.setState({
            modal: {
                show: false
            }
        });
    }

    render(){
        if (this.state.error){
            return <Error />
        }

        if (!this.state.cargar){
            return(
                <Fragment>
                    <Row className="box-element-container"><ElementTetx md={24} rows={3}/></Row>
                    <Row className="box-element-container"><ElementTetx md={24} rows={3}/></Row>
                    <Row className="box-element-container"><ElementTetx md={24} rows={3}/></Row>
                    <center>
                        <Loader style={{marginTop: 10}} content="cargando..." vertical speed="fast" size="md"/>
                    </center>
                </Fragment> 
            );
        }

        let { table } = this.props.search;
        let { data } = this.state;

        return(
            <Grid fluid>
                <ModalSearch data={this.state.modal.data} show={this.state.modal.show} close={this.handleCloseModal}/>
                <span className="title-section">ltimos 20 registros</span>
                {
                    data.map(e => <Item Modal={this.handleOpenModal}
                        key={e.id}
                        id={e.id}
                        title={e.id}
                        date={e.date}
                        data={[
                            [DICT[table].labels[0], e[ DICT[table].labelsObj[0]] ],
                            [DICT[table].labels[1], e[ DICT[table].labelsObj[1]] ],
                            [DICT[table].labels[2], e[ DICT[table].labelsObj[2]] ],
                            [DICT[table].labels[3], e[ DICT[table].labelsObj[3]] ],
                        ]}
                    />)
                }
            </Grid>
        );
            
    }
}

export default Panel1;