import React from 'react';
import Chart from 'react-apexcharts'

function ChartHorizontalBar(props) {
    const data = {
        series: [{
            name: props.label,
            data: props.data
        }],
        options: {
            chart: {
                type: 'bar',
                height: 350
            },
            plotOptions: {
                bar: {
                    horizontal: true,
                },
            },
            dataLabels: {
                enabled: true
            },
            xaxis: {
                categories: props.labels,
            },
            yaxis: {
                labels: {
                    formatter: e => { return e },
                }
            }
        },
    };

    return (
        <Chart options={data.options} series={data.series} type="bar" height={`${props.width}px`} />	
    );
}

export default ChartHorizontalBar;