import React, { Component, Fragment } from 'react';
import URL_API from '../../assets/conf';
import { Grid, Row } from 'rsuite';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';
import ChartHorizontalBar100 from '../charts/HorizontalBar100';
import Error from '../elements/error';

class SecEstrategia extends Component{
    state ={
        _group_: this.props.group,
        cargar: false,
        error: false,
        haOido: {},
        alta: {},
        qpInforcion: {},
        recibirSMS: {},
        cLeerSMS: {},
        cLeerSMS9: {},
        preparadoLRSMS: {},
        preparadoLRSMSPacientes: {},
        qImportanteAnemia: {},
        qImportanteObecidad: {},
        telefonoInfoSalud: {},
        u3MesesSMS: {}
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/nutres/seccion/estrategia/${this.props.group}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                
                return {
                    haOido: await data.haOido.map(e =>{  return parseInt(e)  }),
                    alta: await data.data2.map(e =>{  return parseInt(e.alta)  }),
                    qpInforcion: await data.data2.map(e =>{  return parseInt(e.qpInforcion)  }),
                    recibirSMS: await data.data2.map(e =>{  return parseInt(e.recibirSMS)  }),
                    cLeerSMS: await data.data2.map(e =>{  return parseInt(e.cLeerSMS)  }),
                    cLeerSMS9: await data.data2.map(e =>{  return parseInt(e.cLeerSMS9)  }),
                    preparadoLRSMS: await data.data2.map(e =>{  return parseInt(e.preparadoLRSMS)  }),
                    preparadoLRSMSPacientes: await data.data2.map(e =>{  return parseInt(e.preparadoLRSMSPacientes)  }),
                    qImportanteAnemia: await data.data2.map(e =>{  return parseInt(e.qImportanteAnemia)  }),
                    qImportanteObecidad: await data.data2.map(e =>{  return parseInt(e.qImportanteObecidad)  }),
                    telefonoInfoSalud: await data.data2.map(e =>{  return parseInt(e.telefonoInfoSalud)  }),
                    u3MesesSMS: {
                        1: await data.data3.map(e =>{  return parseInt(e["1"])  }),
                        2: await data.data3.map(e =>{  return parseInt(e["2"])  }),
                        3: await data.data3.map(e =>{  return parseInt(e["3"])  }),
                        4: await data.data3.map(e =>{  return parseInt(e["4"])  }),
                        5: await data.data3.map(e =>{  return parseInt(e["5"])  }),
                        6: await data.data3.map(e =>{  return parseInt(e["6"])  }),
                        7: await data.data3.map(e =>{  return parseInt(e["7"])  }),
                        8: await data.data3.map(e =>{  return parseInt(e["8"])  }),
                        9: await data.data3.map(e =>{  return parseInt(e["9"])  }),
                        99: await data.data3.map(e =>{  return parseInt(e["99"])  }),
                        1111: await data.data3.map(e =>{  return parseInt(e["1111"])  })
                    }
                }
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDataAll();
        
        if (data !== 'error')
        {
            this.setState({
                haOido: {
                    si: await data.haOido.filter(e => e === 1).length,
                    no: await data.haOido.filter(e => e === 2).length,
                    nan: await data.haOido.filter(e => isNaN(e)).length
                },
                alta: {
                    si: await data.alta.filter(e => e === 1).length,
                    no: await data.alta.filter(e => e === 2).length,
                    nan: await data.alta.filter(e => isNaN(e)).length
                },
                qpInforcion: {
                    _1_: await data.qpInforcion.filter(e => e === 1).length,
                    _2_: await data.qpInforcion.filter(e => e === 2).length,
                    _3_: await data.qpInforcion.filter(e => e === 3).length,
                    _4_: await data.qpInforcion.filter(e => e === 4).length,
                    _99_: await data.qpInforcion.filter(e => e === 99).length,
                },
                recibirSMS: {
                    si: await data.recibirSMS.filter(e => e === 1).length,
                    no: await data.recibirSMS.filter(e => e === 2).length,
                    _8666_: await data.recibirSMS.filter(e => e === 8666).length,
                    nan: await data.recibirSMS.filter(e => isNaN(e)).length
                },
                cLeerSMS: {
                    si: await data.cLeerSMS.filter(e => e === 1).length,
                    no: await data.cLeerSMS.filter(e => e === 2).length,
                    _8666_: await data.cLeerSMS.filter(e => e === 8666).length,
                    nan: await data.cLeerSMS.filter(e => isNaN(e)).length
                },
                cLeerSMS9: {
                    si: await data.cLeerSMS9.filter(e => e === 1).length,
                    no: await data.cLeerSMS9.filter(e => e === 2).length,
                    _8666_: await data.cLeerSMS9.filter(e => e === 8666).length,
                    nan: await data.cLeerSMS9.filter(e => isNaN(e)).length
                },
                preparadoLRSMS: {
                    _1_: await data.preparadoLRSMS.filter(e => e === 1).length,
                    _2_: await data.preparadoLRSMS.filter(e => e === 2).length,
                    _3_: await data.preparadoLRSMS.filter(e => e === 3).length,
                    _4_: await data.preparadoLRSMS.filter(e => e === 4).length,
                    nan: await data.preparadoLRSMS.filter(e => isNaN(e)).length,
                },
                preparadoLRSMSPacientes: {
                    _1_: await data.preparadoLRSMSPacientes.filter(e => e === 1).length,
                    _2_: await data.preparadoLRSMSPacientes.filter(e => e === 2).length,
                    _3_: await data.preparadoLRSMSPacientes.filter(e => e === 3).length,
                    _4_: await data.preparadoLRSMSPacientes.filter(e => e === 4).length,
                    nan: await data.preparadoLRSMSPacientes.filter(e => isNaN(e)).length,
                },
                qImportanteAnemia: {
                    _1_: await data.qImportanteAnemia.filter(e => e === 1).length,
                    _2_: await data.qImportanteAnemia.filter(e => e === 2).length,
                    _3_: await data.qImportanteAnemia.filter(e => e === 3).length,
                    _4_: await data.qImportanteAnemia.filter(e => e === 4).length,
                    nan: await data.qImportanteAnemia.filter(e => isNaN(e)).length,
                },
                qImportanteObecidad: {
                    _1_: await data.qImportanteObecidad.filter(e => e === 1).length,
                    _2_: await data.qImportanteObecidad.filter(e => e === 2).length,
                    _3_: await data.qImportanteObecidad.filter(e => e === 3).length,
                    _4_: await data.qImportanteObecidad.filter(e => e === 4).length,
                    nan: await data.qImportanteObecidad.filter(e => isNaN(e)).length,
                },
                telefonoInfoSalud: {
                    si: await data.telefonoInfoSalud.filter(e => e === 1).length,
                    no: await data.telefonoInfoSalud.filter(e => e === 2).length,
                    _8666_: await data.telefonoInfoSalud.filter(e => e === 8666).length,
                    nan: await data.telefonoInfoSalud.filter(e => isNaN(e)).length
                },
                u3MesesSMS: {
                    siEscucho: await data.u3MesesSMS["1"].length,
                    total:  await data.haOido.length,
                    1: {
                        si: await data.u3MesesSMS["1"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["1"].filter(e => e !== 1).length,
                    },
                    2: {
                        si: await data.u3MesesSMS["2"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["2"].filter(e => e !== 1).length,
                    },
                    3: {
                        si: await data.u3MesesSMS["3"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["3"].filter(e => e !== 1).length,
                    },
                    4: {
                        si: await data.u3MesesSMS["4"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["4"].filter(e => e !== 1).length,
                    },
                    5: {
                        si: await data.u3MesesSMS["5"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["5"].filter(e => e !== 1).length,
                    },
                    6: {
                        si: await data.u3MesesSMS["6"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["6"].filter(e => e !== 1).length,
                    },
                    7: {
                        si: await data.u3MesesSMS["7"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["7"].filter(e => e !== 1).length,
                    },
                    8: {
                        si: await data.u3MesesSMS["8"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["8"].filter(e => e !== 1).length,
                    },
                    9: {
                        si: await data.u3MesesSMS["9"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["9"].filter(e => e !== 1).length,
                    },
                    99: {
                        si: await data.u3MesesSMS["99"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["99"].filter(e => e !== 1).length,
                    },
                    1111: {
                        si: await data.u3MesesSMS["1111"].filter(e => e === 1).length,
                        no: await data.u3MesesSMS["1111"].filter(e => e !== 1).length,
                    },
                },
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }
    
    componentDidUpdate = async () =>{
        if (this.props.group !== this.state._group_)
        {
            this.setState({
                cargar: false,
                _group_: this.props.group
            });
            
            await this.handleGetData();
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }
        
        // if (!this.state.cargar){
        //     return <h1>Cargando...</h1>
        // }

        return (
            <Fragment>
                <Grid fluid style={{margin:0, padding:0}}>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Ha odo sobre la estrategia NUTRES?" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    data={[this.state.haOido.si, this.state.haOido.no, this.state.haOido.nan]} 
                                    labels={["Si", "No", "Sin resp."]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                        { this.state.cargar ?
                            <Element title="Completo el alta en NUTRES?" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["Si", "No", "Sin resp."]} 
                                    data={[this.state.alta.si, this.state.alta.no, this.state.alta.nan]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Qu le pareci la informacin?" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    data={[
                                        this.state.qpInforcion._1_, 
                                        this.state.qpInforcion._2_, 
                                        this.state.qpInforcion._3_,
                                        this.state.qpInforcion._4_,
                                        this.state.qpInforcion._99_,
                                    ]} 
                                    labels={[
                                        "Poco claro",
                                        "Algo claro",
                                        "Claro",
                                        "Muy claro",
                                        "No se recibi informacin"
                                    ]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                        { this.state.cargar ?
                            <Element title="Es posible recibir SMS en su telfono?" md={12} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    labels={["Si", "No",  "No sabe", "Sin resp."]} 
                                    data={[
                                        this.state.recibirSMS.si, 
                                        this.state.recibirSMS.no, 
                                        this.state.recibirSMS._8666_, 
                                        this.state.recibirSMS.nan
                                    ]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Cree que leera los SMS de NUTRES?" md={12} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    labels={["Si", "No",  "No sabe", "Sin resp."]} 
                                    data={[
                                        this.state.cLeerSMS.si, 
                                        this.state.cLeerSMS.no, 
                                        this.state.cLeerSMS._8666_, 
                                        this.state.cLeerSMS.nan
                                    ]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                        { this.state.cargar ?
                            <Element title="Cree que leera los SMS de NUTRES (9 meses)?" md={12} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    labels={["Si", "No",  "No sabe", "Sin resp."]} 
                                    data={[
                                        this.state.cLeerSMS9.si, 
                                        this.state.cLeerSMS9.no, 
                                        this.state.cLeerSMS9._8666_, 
                                        this.state.cLeerSMS9.nan
                                    ]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Se siente preparad@ para leer/revisar los SMS?" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    labels={[
                                        "Nada preparad@", 
                                        "Algo preparad@",  
                                        "Estoy preparad@", 
                                        "Muy preparad@", 
                                        "Sin resp."]} 
                                    data={[
                                        this.state.preparadoLRSMS._1_, 
                                        this.state.preparadoLRSMS._2_, 
                                        this.state.preparadoLRSMS._3_, 
                                        this.state.preparadoLRSMS._4_, 
                                        this.state.preparadoLRSMS.nan
                                    ]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                        { this.state.cargar ?
                            <Element title="Qu tan preparado se siente para usar los SMS en consultas?" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    labels={[
                                        "Nada preparad@", 
                                        "Algo preparad@",  
                                        "Estoy preparad@", 
                                        "Muy preparad@", 
                                        "Sin resp."]} 
                                    data={[
                                        this.state.preparadoLRSMSPacientes._1_, 
                                        this.state.preparadoLRSMSPacientes._2_, 
                                        this.state.preparadoLRSMSPacientes._3_, 
                                        this.state.preparadoLRSMSPacientes._4_, 
                                        this.state.preparadoLRSMSPacientes.nan
                                    ]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Qu tan importante cree que es el problema de anemia/desnutricin?" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    labels={[
                                        "Nada importante", 
                                        "Algo importante",  
                                        "Importante", 
                                        "Muy importante", 
                                        "Sin resp."
                                    ]} 
                                    data={[
                                        this.state.qImportanteAnemia._1_, 
                                        this.state.qImportanteAnemia._2_, 
                                        this.state.qImportanteAnemia._3_, 
                                        this.state.qImportanteAnemia._4_, 
                                        this.state.qImportanteAnemia.nan
                                    ]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                        { this.state.cargar ?
                            <Element title="Qu tan importante cree que es el problema de sobrepeso/obecidad?" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    labels={[
                                        "Nada importante", 
                                        "Algo importante",  
                                        "Importante", 
                                        "Muy importante", 
                                        "Sin resp."
                                    ]} 
                                    data={[
                                        this.state.qImportanteObecidad._1_, 
                                        this.state.qImportanteObecidad._2_, 
                                        this.state.qImportanteObecidad._3_, 
                                        this.state.qImportanteObecidad._4_, 
                                        this.state.qImportanteObecidad.nan
                                    ]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    <span className="title-section">Uso de telefona celular</span>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Utilizar el telfono celular para conseguir informacin sobre salud/nutricin?" md={24} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    labels={[
                                        "Si", 
                                        "No",  
                                        "No sabe",
                                        "Sin resp."
                                    ]} 
                                    data={[
                                        this.state.telefonoInfoSalud.si, 
                                        this.state.telefonoInfoSalud.no, 
                                        this.state.telefonoInfoSalud._8666_, 
                                        this.state.telefonoInfoSalud.nan
                                    ]}
                                />
                            </Element>
                            :<Element md={24}/>
                        }
                    </Row>

                    <span className="title-section">Exposicin de mensajes Nutres/Covid-19</span>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Escucho algn mensaje relacionado con la campaa de salud/alimentacin infantil/covid-19?" md={24} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    labels={[
                                        "Si", 
                                        "No/No sabe/No recuerda/No resp.",
                                    ]} 
                                    data={[
                                        this.state.u3MesesSMS.siEscucho, 
                                        (this.state.u3MesesSMS.total - this.state.u3MesesSMS.siEscucho),
                                    ]}
                                />
                            </Element>
                            :<Element md={24}/>
                        }
                    </Row>

                    {/* Graficas sobre nutres*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Qu mensaje recuerda sobre la campaa alimentacin infantil/Covid-19?" md={24} fill={1}>
                                <ChartHorizontalBar100
                                    msgY="Personas"
                                    height={600}
                                    data={[
                                        {
                                            name: "Si recuerda",
                                            data: [
                                                this.state.u3MesesSMS["1"].si,
                                                this.state.u3MesesSMS["2"].si,
                                                this.state.u3MesesSMS["3"].si,
                                                this.state.u3MesesSMS["4"].si,
                                                this.state.u3MesesSMS["5"].si,
                                                this.state.u3MesesSMS["6"].si,
                                                this.state.u3MesesSMS["7"].si,
                                                this.state.u3MesesSMS["8"].si,
                                                this.state.u3MesesSMS["9"].si,
                                                this.state.u3MesesSMS["99"].si,
                                                this.state.u3MesesSMS["1111"].si,
                                            ]
                                        },{
                                            name: "No recuerda",
                                            data: [
                                                this.state.u3MesesSMS["1"].no,
                                                this.state.u3MesesSMS["2"].no,
                                                this.state.u3MesesSMS["3"].no,
                                                this.state.u3MesesSMS["4"].no,
                                                this.state.u3MesesSMS["5"].no,
                                                this.state.u3MesesSMS["6"].no,
                                                this.state.u3MesesSMS["7"].no,
                                                this.state.u3MesesSMS["8"].no,
                                                this.state.u3MesesSMS["9"].no,
                                                this.state.u3MesesSMS["99"].no,
                                                this.state.u3MesesSMS["1111"].no,
                                            ]
                                        }
                                    ]}
                                    labels={[
                                        "Lactancia materna exclusiva o cualquier tipo",
                                        "Alimentacin complementaria adecuada",
                                        "Aumentar el consumo de frutas y verduras",
                                        "Evitar/disminuir el consumo de alimentos industrializados, ultra-procesados/comida chatarra",
                                        "Evitar/disminuir el consumo de bebidas azucaradas",
                                        "Aumentar el consumo de agua natural potable",
                                        "Aumentar/conservar la prctica de actividad fsica (toda la familia)",
                                        "Consumir suplementos o micronutrimentos ",
                                        "Acudir a vigilancia a su centro de salud",
                                        "No sabe/No responde",
                                        "Otro",
                                    ]}
                                />
                            </Element>
                            :<Element md={24}/>
                        }
                    </Row>

                </Grid>
            </Fragment>
        );
    }
}

export default SecEstrategia;