import React, { Component, Fragment } from 'react';
import Panel1 from './panel1';
import Panel2 from './Panel2';

class Nutres extends Component{
    render(){
        return(
            <Fragment>
                <section className="main_panel1">
                    <section className="panel_contect">
                        <Panel1 />
                    </section>
                </section>
                <section className="main_panel2">
                    <section className="panel_contect">
                        <Panel2 />
                    </section>
                </section>
            </Fragment>
        );
    }
}

export default Nutres;