import React, { Component } from 'react';
import { Grid, Row, Progress } from 'rsuite';
import URL_API from  '../../assets/conf';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';
import Error from '../elements/error';

const { Circle } = Progress;

class Panel1 extends Component{
    state = {
        error: false,
        length: 0,
        createdOn: {},
        generoMama: {},
        edadMama: {},
        edadBebe: {},
        parentescoBebe: {},
        entidad: {}, 
        lactanciaBebe: {}, 
        segmentacionMama: [],
        calSemanasNacimiento: [],
        cargar: false,
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/cuidadoras`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                return {
                    length: data.length,
                    generoMama: data.map(e => { return parseInt(e.generoMama) }),
                    edadMama: data.map(e => { return parseInt(e.edadMama) }),
                    segmentacionMama: data.map(e => { return parseInt(e.segmentacionMama) }),
                    parentescoBebe: data.map(e => { return parseInt(e.parentescoBebe) }),
                    edadbebe: data.map(e => { return parseInt(e.edadBebe) }),
                    entidad: data.map(e => { return parseInt(e.entidad) }), 
                    lactanciaBebe: data.map(e => { return parseInt(e.lactanciaBebe) }), 
                    calSemanasNacimiento: data.map(e => { return parseInt(e.calSemanasNacimiento) }) 
                }
            }
        } catch (error) {
            return "error"
        }
    }

    getDataChartEdadMama = async (data) =>{
        this.setState({
            edadMama: {
                _1_: data.filter(e => e >= 11 && e <= 20).length,
                _2_: data.filter(e => e >= 21 && e <= 30).length,
                _3_: data.filter(e => e >= 31 && e <= 40).length,
                _4_: data.filter(e => e >= 41 && e <= 50).length,
                _5_: data.filter(e => e >= 51).length
           }
        });
    }

    getDataChartEdadBebe = async (data) =>{
        this.setState({
            edadBebe: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e === 4).length,
                _5_: data.filter(e => e === 5).length,
                nan: data.filter(e => isNaN(e)).length
           }
        });
    }

    getDataChartParentescoBebe = async (data) =>{
        this.setState({
            parentescoBebe: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e === 4).length,
                _5_: data.filter(e => e === 5).length,
                nan: data.filter(e => isNaN(e)).length
           }
        });
    }

    getDataChartGeneroMama = async (data) =>{
        let mujeres = data.filter(e => e === 1).length;
        let hombres = data.filter(e => e === 2).length;
        let otros = data.filter(e => e !== 1 && e !== 2).length;

        this.setState({
            generoMama: {
                mujeres,
                hombres,
                otros
            }
        });
    }

    getDataChartEntidad = async (data) =>{
        let porcentaje = 100/data.length;
        this.setState({
            entidad: {
                morelos: ((data.filter(e => e === 1).length) * porcentaje).toFixed(2),
                yucatan: ((data.filter(e => e === 2).length) * porcentaje).toFixed(2),
           }
        });
    }

    getDataChartLactanciaBebe = async (data) =>{
        this.setState({
            lactanciaBebe: {
                si: data.filter(e => e === 1).length,
                no: data.filter(e => e === 2).length,
                nan: data.filter(e => isNaN(e)).length
           }
        });
    }
    
    getDataChartSegmentacionMama = async (data) =>{
        this.setState({
            segmentacionMama: {
                embarazada: data.filter(e => e === 1).length,
                boy: data.filter(e => e === 2).length,
                girl: data.filter(e => e === 3).length
           }
        });
    }

    async componentDidMount(){
        let data = await this.fetchDataAll();

        if (data !== "error")
        {
            await this.getDataChartGeneroMama(data.generoMama);
            await this.getDataChartEdadMama(data.edadMama);
            await this.getDataChartEdadBebe(data.edadbebe);
            await this.getDataChartParentescoBebe(data.parentescoBebe);
            await this.getDataChartEntidad(data.entidad);
            await this.getDataChartLactanciaBebe(data.lactanciaBebe);
            await this.getDataChartSegmentacionMama(data.segmentacionMama);

            this.setState({
                length: data.length,
                cargar: true
            });
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }

        return(
            <Grid fluid style={{margin:0, padding:0}}>

                {/* Porcentajes morelos/yucatan */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="Registros Morelos" md={12} fill={1}>
                            <Circle style={{width: "30%", margin:10}} percent={parseFloat(this.state.entidad.morelos)} strokeColor="#ffc107" />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargar ?
                        <Element title="Registros Yucatan" md={12} fill={1}>
                            <Circle style={{width: "30%", margin:10}} percent={parseFloat(this.state.entidad.yucatan)} strokeColor="#b3b3ff" />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>
                
                {/* Graficas de barras genero y parentesco */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="Gnero de cuidadoras/res" md={11} fill={1}>
                            <ChartBar 
                                width={200}
                                label="Registros" 
                                data={[this.state.generoMama.mujeres, this.state.generoMama.hombres, this.state.generoMama.otros]} 
                                labels={["Mujeres", "Hombres", "Sin responder"]}
                            />
                        </Element>
                        :<Element md={11}/>
                    }
                    { this.state.cargar ?
                        <Element title="Parentesco bebe" md={13} fill={1}>
                            <ChartHorizontalBar 
                                width={200}
                                label="Registros"
                                labels={["Hij@", "Niet@", "Sobrin@", "Herman@", "Otr@", "Sin responder"]} 
                                data={[this.state.parentescoBebe._1_, this.state.parentescoBebe._2_, this.state.parentescoBebe._3_, this.state.parentescoBebe._4_, this.state.parentescoBebe._5_, this.state.parentescoBebe.nan]}
                            />
                        </Element>
                        : <Element md={13} />
                    }
                </Row>

                {/* Graficas de edades (cuidador/bebe) */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="Frecuencia de edades (cuidadoras/res)" md={12} fill={1}>
                            <ChartPie 
                                width={250}
                                labels={["11-20 aos", "21-30 aos", "31-40 aos", "41-50 aos", "+50 aos"]} 
                                data={[this.state.edadMama._1_, this.state.edadMama._2_, this.state.edadMama._3_, this.state.edadMama._4_, this.state.edadMama._5_]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargar ?
                        <Element title="Frecuencia de edades (bebe)" md={12} fill={1}>
                            <ChartPie 
                                width={250}
                                labels={["0-5 meses", "6-8 meses", "9-11 meses", "1-2 aos", "2-5 aos", "Sin responder"]} 
                                data={[this.state.edadBebe._1_, this.state.edadBebe._2_, this.state.edadBebe._3_, this.state.edadBebe._4_, this.state.edadBebe._5_, this.state.edadBebe.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                {/*  */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="segmentacin" md={13} fill={1}>
                            <ChartBar 
                                width={200}
                                label="Registros" 
                                data={[this.state.segmentacionMama.embarazada, this.state.segmentacionMama.boy, this.state.segmentacionMama.girl]} 
                                labels={["Embarazada", "Nio", "Nia"]}
                            />
                        </Element>
                        : <Element md={13} />
                    }
                    { this.state.cargar ?
                        <Element title="Toma pecho?(verificar s->1, n->2)" md={11} fill={1}>
                            <ChartPie 
                                width={200}
                                data={[this.state.lactanciaBebe.si, this.state.lactanciaBebe.no, this.state.lactanciaBebe.nan]} 
                                labels={["Si", "No", "Sin responder"]}
                            />
                        </Element>
                        : <Element md={11} />
                    }
                </Row>
            </Grid>
        );  
    }
}

export default Panel1;