const fData = (table ,data) => {
    let dataTemp = [];

    if (table === 1){
        dataTemp = [
            ["rea unidad", data.areaUnidad],
            ["Entidad", data.entidad],
            ["Grupo", data.grupo],
            ["Cargo", data.cargo],
            ["Encargado de US", data.esEncargado],
            ["Remplazo/Apoyo por covid-19 en la US", data.remplazo],
            ["Sexo", data.sexo],
            ["Estado civil", data.estadoCilvil],
            ["Tipo de contrato", data.tipoContrato],
            ["Tipo de institucin", data.tipoInstitucion],
            ["Edad", data.edad + " Aos"],
            ["Aos tranajando", data.yearsTrabajando],
        ];

        return dataTemp;
    }
    
    dataTemp = [
        ["Entidad", data.entidad],
        ["Grupo", data.grupo],
        ["rea unidad", data.areaUnidad],
        ["Beneficiari@ INSABI", data.bInsabi],
        ["Edad madre", data.edadM + " os"],
        ["Edad ni@/bebe", data.edadBebe + " Meses"],
        ["Sexo hih@/bebe", data.sexoHijo],
        ["Embarazada actualmente", data.embarazada],
    ];

    return dataTemp;
}

export default fData;