import React from 'react';
import { Modal, Button } from 'rsuite';
import moment from 'moment';
import 'moment/locale/es-mx';   

moment.locale('es-mx');

function ModalSearch (props) {
    let i = 0;

    if (!props.show){
        return ""
    }

    return(
        <Modal show={props.show} onHide={props.close}>
            <Modal.Header>
                <Modal.Title>{props.data.title}</Modal.Title>
                <span>{moment(props.data.date).fromNow()}</span>
            </Modal.Header>
            <Modal.Body>
                {
                    <table>
                        <tbody>
                            { props.data.items.map( e =>
                                <tr key={`${props.data.id}-${i++}`}>
                                    <td style={{ fontWeight: 'bold', width: "40%" }}>{e[0]}:</td>
                                    <td>{e[1]}</td>
                                </tr>
                                )
                            }
                        </tbody>
                    </table>
                }
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={props.close} appearance="primary">
                    Cerrar
                </Button>
            </Modal.Footer>
        </Modal>
    );
}

export default ModalSearch;