import React, { Component, Fragment } from 'react';
import URL_API from '../../assets/conf';
import { Grid, Row, Progress } from 'rsuite';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';
import Error from '../elements/error';

const { Circle } = Progress;

class SecGeneral extends Component{
    state ={
        _group_: this.props.group,
        cargar: false,
        error: false,
        length: 0,
        entidad: {},
        grupo: {},
        areaUnidad: {},
        tipoInstitucion: {},
        sexo: {},
        estadoCilvil: {},
        edad: {},
        cargo: {},
        tipoContrato: {},
        remplazo: {},
        yearsTrabajando: {},
        esEncargado: {},
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/nutres/seccion/general/${this.props.group}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                
                return {
                    entidad: await data.map(e =>{  return parseInt(e.entidad)  }),
                    grupo: await data.map(e =>{  return parseInt(e.grupo)  }),
                    areaUnidad: await data.map(e =>{  return parseInt(e.areaUnidad)  }),
                    tipoInstitucion: await data.map(e =>{  return parseInt(e.tipoInstitucion)  }),
                    sexo: await data.map(e =>{  return parseInt(e.sexo)  }),
                    estadoCilvil: await data.map(e =>{  return parseInt(e.estadoCilvil)  }),
                    edad: await data.map(e =>{  return parseInt(e.edad)  }),
                    cargo: await data.map(e =>{  return parseInt(e.cargo)  }),
                    tipoContrato: await data.map(e =>{  return parseInt(e.tipoContrato)  }),
                    remplazo: await data.map(e =>{  return parseInt(e.remplazo)  }),
                    yearsTrabajando: await data.map(e =>{  return parseInt(e.yearsTrabajando)  }),
                    esEncargado: await data.map(e =>{  return parseInt(e.esEncargado)  })
                }
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDataAll();

        if (data !== 'error')
        {
            this.setState({
                length: data.entidad.length,
                entidad: {
                    morelos: await data.entidad.filter(e => e === 17).length,
                    yucatan: await data.entidad.filter(e => e === 31).length,
                },
                grupo: {
                    control: await data.grupo.filter(e => e === 1).length,
                    intervencion: await data.grupo.filter(e => e === 2).length,
                },
                areaUnidad: {
                    rural: await data.areaUnidad.filter(e => e === 1).length,
                    urbana: await data.areaUnidad.filter(e => e === 2).length,
                },
                tipoInstitucion: {
                    ssa: await data.tipoInstitucion.filter(e => e === 1).length,
                    insabi: await data.tipoInstitucion.filter(e => e === 2).length,
                    nan: await data.tipoInstitucion.filter(e => e !== 1 && e !== 2).length,
                },
                sexo: {
                    hombre: await data.sexo.filter(e => e === 1).length,
                    mujer: await data.sexo.filter(e => e === 2).length,
                },
                estadoCilvil: {
                    casado: await data.estadoCilvil.filter(e => e === 1).length,
                    soltero: await data.estadoCilvil.filter(e => e === 2).length,
                    nan: await data.estadoCilvil.filter(e => e !== 1 && e !== 2).length,
                },
                edad: {
                    _1_: await data.edad.filter(e => e <= 20).length,
                    _2_: await data.edad.filter(e => e >= 21 && e <= 30).length,
                    _3_: await data.edad.filter(e => e >= 31 && e <= 40).length,
                    _4_: await data.edad.filter(e => e >= 41).length,
                    nan: await data.edad.filter(e => isNaN(e)).length,
                },
                cargo: {
                    _1_: await data.cargo.filter(e => e === 1).length,
                    _2_: await data.cargo.filter(e => e === 2).length,
                    _3_: await data.cargo.filter(e => e === 3).length,
                    _4_: await data.cargo.filter(e => e === 4).length,
                    _1111_: await data.cargo.filter(e => e === 1111).length,
                },
                tipoContrato: {
                    _1_: await data.tipoContrato.filter(e => e === 1).length,
                    _2_: await data.tipoContrato.filter(e => e === 2).length,
                    _3_: await data.tipoContrato.filter(e => e === 3).length,
                    _4_: await data.tipoContrato.filter(e => e === 4).length,
                    _1111_: await data.tipoContrato.filter(e => e === 1111).length,
                },
                remplazo: {
                    si: await data.remplazo.filter(e => e === 1).length,
                    no: await data.remplazo.filter(e => e === 2).length,
                },
                yearsTrabajando: {
                    _1_: await data.yearsTrabajando.filter(e => e >= 0 && e <= 2).length,
                    _2_: await data.yearsTrabajando.filter(e => e >= 3 && e <= 5).length,
                    _3_: await data.yearsTrabajando.filter(e => e >= 6).length,
                },
                esEncargado: {
                    si: await data.esEncargado.filter(e => e === 1).length,
                    no: await data.esEncargado.filter(e => e === 2).length,
                },
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }
    
    componentDidUpdate = async () =>{
        if (this.props.group !== this.state._group_)
        {
            this.setState({
                cargar: false,
                _group_: this.props.group
            });
            
            await this.handleGetData();
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }
        
        // if (!this.state.cargar){
        //     return <h1>Cargando...</h1>
        // }

        return (
            <Fragment>
                <Grid fluid style={{margin:0, padding:0}}>
                    {/* Porcentajes morelos/yucatan */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Registros Morelos" md={12} fill={1}>
                                <Circle  
                                style={{width: "30%", margin:10}} 
                                percent={parseFloat((this.state.entidad.morelos * (100 / this.state.length)).toFixed(2))}
                                strokeColor="#ffc107" 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Registros Yucatan" md={12} fill={1}>
                                <Circle 
                                style={{width: "30%", margin:10}} 
                                percent={parseFloat((this.state.entidad.yucatan * (100 / this.state.length)).toFixed(2))} 
                                strokeColor="#b3b3ff" 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas de grupo, Area unidad y Tipo de institucion*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Grupo" md={8} fill={1}>
                                <ChartPie
                                    width={150}
                                    data={[this.state.grupo.control, this.state.grupo.intervencion]} 
                                    labels={["Control", "Intervencin"]}
                                />
                            </Element>
                            :<Element md={8}/>
                        }
                        { this.state.cargar ?
                            <Element title="rea de unidad" md={8} fill={1}>
                                <ChartPie
                                    width={150}
                                    labels={["Rural", "Urbana"]} 
                                    data={[this.state.areaUnidad.rural, this.state.areaUnidad.urbana]}
                                />
                            </Element>
                            : <Element md={8} />
                        }
                        { this.state.cargar ?
                            <Element title="Tipo de institucin" md={8} fill={1}>
                                <ChartPie
                                    width={150}
                                    labels={["SSA", "INSABI", "Sin resp."]} 
                                    data={[this.state.tipoInstitucion.ssa, this.state.tipoInstitucion.insabi, this.state.tipoInstitucion.nan]}
                                />
                            </Element>
                            : <Element md={8} />
                        }
                    </Row>

                    <span className="title-section">Datos del entrevistado</span>
                    
                    {/* Graficas de Edad hijo y sexo hijo */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Sexo" md={11} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    data={[this.state.sexo.hombre, this.state.sexo.mujer]} 
                                    labels={["Hombre", "Mujer"]}
                                />
                            </Element>
                            :<Element md={11}/>
                        }
                        { this.state.cargar ?
                            <Element title="Edad" md={13} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["<20 Aos", "21-30 Aos", "31-40 Aos", "41+ Aos", "Sin resp."]}
                                    data={[this.state.edad._1_, this.state.edad._2_, this.state.edad._3_, this.state.edad._4_, this.state.edad.nan]}
                                />
                            </Element>
                            : <Element md={13} />
                        }
                    </Row>

                    {/* Graficas de Estado civil/Cargo */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Estado civil" md={12} fill={1}>
                                <ChartHorizontalBar
                                    width={200}
                                    label="Registros"
                                    labels={["Casado@/Unin libre", "Solter@/Divorciad@/Separad@/Viud@", "Sin resp."]} 
                                    data={[this.state.estadoCilvil.casado, this.state.estadoCilvil.soltero, this.state.estadoCilvil.nan]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Cargo" md={12} fill={1}>
                                <ChartHorizontalBar
                                    width={200}
                                    label="Registros" 
                                    labels={["Mdico", "Enfermera", "Pasante", "Promotor/Auxiliar", "Otro"]}
                                    data={[this.state.cargo._1_, this.state.cargo._2_, this.state.cargo._3_, this.state.cargo._4_, this.state.cargo._1111_]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas de Tipo de contrato/Remplazo */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Tipo de contrado" md={12} fill={1}>
                                <ChartHorizontalBar
                                    width={200}
                                    label="Registros"
                                    labels={["Base", "Servicio social", "Contrato/Eventual", "Confianza", "Otro"]}
                                    data={[this.state.tipoContrato._1_, this.state.tipoContrato._2_, this.state.tipoContrato._3_, this.state.tipoContrato._4_, this.state.tipoContrato._1111_]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Es remplazo/apoyo en esta clinica por COVID 19?" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["Si", "No"]}
                                    data={[this.state.remplazo.si, this.state.remplazo.no]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    <span className="title-section">Tiempo trabajando en la unidad de salud</span>

                     {/* Graficas de Aos trabajando/Encargado */}
                     <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Tiempo trabajando" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["0-2 Aos", "3-5 Aos", "6+ Aos"]}
                                    data={[this.state.yearsTrabajando._1_, this.state.yearsTrabajando._2_, this.state.yearsTrabajando._3_]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Es encargado de la unidad de salud?" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["Si", "No"]}
                                    data={[this.state.esEncargado.si, this.state.esEncargado.no]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                </Grid>
            </Fragment>
        );
    }
}

export default SecGeneral;