import React, { Component, Fragment } from 'react';
import { Row, Grid } from 'rsuite';
import ElementData from '../elements/ElementDataBox';
import URL_API from '../../assets/conf';

class Panel2 extends Component{
    state = {
        cargar: false,
        error: false,
        data: {}
    }

    fetchDates = async () =>{
        try {
            let resp = await fetch(`${URL_API}/home`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                return {
                    data: data.data,
                };
            }
        } catch (error) {
            return "error";
        }
    }
    
    handleGetData = async () =>{
        let data = await this.fetchDates();
        if (data !== 'error')
        {
            this.setState({
                data: data.data,
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }

    render(){
        if (this.state.error){
            return ""
        }

        return(
            <Fragment>
                <Grid fluid style={{margin:0, padding:0}}>
                    <Row className="box-element-container">
                        <ElementData md={24} idx={7} cargar={this.state.cargar} data={this.state.data.mBeneficiarioINSABI} of={this.state.data.mlength}/>
                    </Row>
                    <Row className="box-element-container">
                        <ElementData md={24} idx={8}cargar={this.state.cargar} data={this.state.data.mEmbarazada} of={this.state.data.mlength}/>
                    </Row>
                    <Row className="box-element-container">
                        <ElementData md={24} idx={10}cargar={this.state.cargar} data={this.state.data.mSexoHijoMujer} of={this.state.data.mlength}/>
                    </Row>
                    <Row className="box-element-container">
                        <ElementData md={24} idx={9}cargar={this.state.cargar} data={this.state.data.mSexoHijoHombre} of={this.state.data.mlength}/>
                    </Row>
                </Grid>
            </Fragment>
        );
    }
}

export default Panel2;