import React from 'react';
import Chart from 'react-apexcharts'

function HorizontalBar100(props){
    const data = {
        series: props.data,
        options: {
            chart: {
                type: 'bar',
                height: 350,
                stacked: true,
                stackType: '100%'
            },
            plotOptions: {
                bar: {
                    horizontal: true,
                },
            },
            stroke: {
                width: 1,
                colors: ['#fff']
            },
            xaxis: {
                categories: props.labels
            },
            tooltip: {
                y: {
                    formatter: function (val) {
                        return val + " " + props.msgY
                    }
                }
            },
            fill: {
                opacity: 1
            },
            legend: {
                position: 'top',
                horizontalAlign: 'center',
                offsetX: 40
            }
        },
      };

	return (
        <Chart options={data.options} series={data.series} type="bar" height={props.height} />		
	);
}

export default HorizontalBar100;