import React from 'react';
import { Row } from 'rsuite';
import Element from './Element';
import ChartLine from '../charts/LineCreatedOn';
import IconNew from '../../assets/icons/new.png';
import IconRegistros from '../../assets/icons/registros.png';

function RowTop(props){
    const { length, createdOn } = props;

    return(
        <Row className="box-element-container">
            <Element style={{"background":"#006666"}} md={8} fill={1}>
                <div className="box-element-top-content">
                    <div className="box-element-top-icon">
                        <img src={IconRegistros} alt="Icono registros" />
                    </div>
                    <div className="box-element-top-text">
                        <span>{length}</span>
                        <span>Registros totales</span>
                    </div>
                </div>
            </Element>

            <Element color={1} title="Actividad" style={{"background":"#339966"}} md={8} fill={1}>
                <ChartLine data1={createdOn}/>
            </Element>

            <Element style={{"background":"#339933"}} md={8} fill={1}>
                <div className="box-element-top-content">
                    <div className="box-element-top-icon">
                        <img src={IconNew} alt="Icono nuevos registros" />
                    </div>
                    <div className="box-element-top-text">
                        <span>{createdOn.value[createdOn.value.length - 1]}</span>
                        <span>Registros acumulados {createdOn.name[createdOn.name.length - 1]}</span>
                    </div>
                </div>
            </Element>
        </Row>
    );
}

export default RowTop;