import React, { Component, Fragment } from 'react';
import URL_API from '../../assets/conf';
import { Grid, Row } from 'rsuite';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';
import Error from '../elements/error';

class SecGeneralM extends Component{
    state ={
        _group_: this.props.group,
        cargar: false,
        data: {},
        error: false,
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/madres/seccion/more/${this.props.group}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                
                return data
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDataAll();

        if (data !== 'error')
        {
            this.setState({
                data: data,
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }
    
    componentDidUpdate = async () =>{
        if (this.props.group !== this.state._group_)
        {
            this.setState({
                cargar: false,
                _group_: this.props.group
            });
            
            await this.handleGetData();
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }
        
        // if (!this.state.cargar){
        //     return <h1>Cargando...</h1>
        // }

        let {data} = this.state;
            
        return (
            <Fragment>
                <Grid fluid style={{margin:0, padding:0}}>
                    {/* Graficas de parentesco/Numero de personas en el hogar */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Parentesco ni@" md={12} fill={1}>
                                <ChartHorizontalBar 
                                    width={250}
                                    label="Registros"
                                    labels={[
                                        "Madre", 
                                        "Abuela", 
                                        "Tia/Hermana", 
                                        "Padre/Abuelo", 
                                        "Tio/Hermano", 
                                        "Ningn parentesco", 
                                        "Otro parentesco", 
                                        "No sabe/Sin resp."
                                    ]} 
                                    data={[
                                        data.parentesco._1_, 
                                        data.parentesco._2_, 
                                        data.parentesco._3_, 
                                        data.parentesco._4_, 
                                        data.parentesco._5_, 
                                        data.parentesco._6_, 
                                        data.parentesco.otro, 
                                        data.parentesco.nan
                                    ]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Nmero de personas en el hogar" md={12} fill={1}>
                                <ChartBar 
                                    width={250}
                                    label="Registros" 
                                    labels={["1-2 Personas", "3-4 Personas", "5-6 Personas", "+7 Personas", "Sin resp."]}
                                    data={[data.nPersonasHogar._1_, data.nPersonasHogar._2_, data.nPersonasHogar._3_, data.nPersonasHogar._4_, data.nPersonasHogar.nan]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas de Posicion hijo/Numero de menores */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Posicin ni@" md={12} fill={1}>
                                <ChartBar 
                                    width={200}
                                    label="Registros"
                                    labels={["P. 1", "P. 2", "P. 3", "P. +4", "Sin resp."]}
                                    data={[data.posicionHijo._1_, data.posicionHijo._2_, data.posicionHijo._3_, data.posicionHijo._4_, data.posicionHijo.nan]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Menores de 5 aos (hogar)" md={12} fill={1}>
                                <ChartBar 
                                    width={200} 
                                    label="Registros" 
                                    labels={["0 M.", "1 M.", "2 M.", "+3 M.", "Sin resp."]}
                                    data={[data.nMenores5._1_, data.nMenores5._2_, data.nMenores5._3_, data.nMenores5._4_, data.nMenores5.nan]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>
                    
                    {/* Graficas de Estado civil/Escolaridad */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Estado civil" md={12} fill={1}>
                                <ChartHorizontalBar
                                    width={300}
                                    label="Registros"
                                    labels={[
                                        "Soltera", 
                                        "Casada", 
                                        "Vive en unin libre", 
                                        "Separada", 
                                        "Divorciada", 
                                        "Viuda", 
                                        "Sin resp."
                                    ]} 
                                    data={[
                                        data.estadoCivil._1_,
                                        data.estadoCivil._2_,
                                        data.estadoCivil._3_, 
                                        data.estadoCivil._4_, 
                                        data.estadoCivil._5_, 
                                        data.estadoCivil._6_, 
                                        data.estadoCivil.nan
                                    ]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Escolaridad" md={12} fill={1}>
                                <ChartHorizontalBar 
                                    width={300}
                                    label="Registros" 
                                    labels={[
                                        "Preescolar/Kinder", 
                                        "Primaria", 
                                        "Secundaria", 
                                        "Preparatoria/Bachillerato",
                                        "Normal bsica", 
                                        "Carrera tcnica/comercial", 
                                        "Profesional (universidad)", 
                                        "Posgrado", 
                                        "Ninguno", 
                                        "No sabe/Sin resp."
                                    ]}
                                    data={[
                                        data.escolaridad._1_, 
                                        data.escolaridad._2_, 
                                        data.escolaridad._3_, 
                                        data.escolaridad._4_, 
                                        data.escolaridad._5_, 
                                        data.escolaridad._6_, 
                                        data.escolaridad._7_, 
                                        data.escolaridad._8_, 
                                        data.escolaridad.ninguno,
                                        data.escolaridad.nan
                                    ]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas de Dedicacion/Programa socials */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Principal actividad" md={12} fill={1}>
                                <ChartHorizontalBar
                                    width={200}
                                    label="Registros"
                                    labels={["Ama de casa", "Trabajadora/Empleada", "Estudiante", "Otro", "Sin resp."]} 
                                    data={[data.dedicacion._1_, data.dedicacion._2_, data.dedicacion._3_, data.dedicacion.otro, data.dedicacion.nan]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Pertenencia a programa social" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["Si", "No", "No sabe/Sin resp."]}
                                    data={[data.pProgramaSocial.si, data.pProgramaSocial.no, data.pProgramaSocial.nan]} 
                                />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                </Grid>
            </Fragment>
        );
    }
}

export default SecGeneralM;