import React from 'react';
import Element from '../elements/Element';
import DATAHOME from '../../assets/HomeDataElement';

function ElementData(props) {
    if (props.cargar){
        return(
            <Element style={{"background": DATAHOME[props.idx].colorB}} md={props.md} fill={1}>
                <div className="box-element-top-content">
                    <div className="box-element-top-icon">
                        <img src={DATAHOME[props.idx].img} alt={DATAHOME[props.idx].alt} />
                    </div>
                    <div className="box-element-top-text">
                        <span>{props.data}</span>
                        <span>{DATAHOME[props.idx].obj} de {props.of} {DATAHOME[props.idx].msg}</span>
                    </div>
                </div>
            </Element>
        );
    }

    return <Element style={{"background": props.colorB}} md={props.md}/>
}

export default ElementData;