import React, { Component } from 'react';
import { Grid, Row, Progress } from 'rsuite';
import URL_API from  '../../assets/conf';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';
import Error from '../elements/error';

const { Circle } = Progress;

class SecGeneral extends Component{
    state = {
        error: false,
        length: 0,
        genero: {},
        edad: {},
        estado: {},
        ocupacion: {},
        tiempoTrabajando: {},
        tipoContrato: {},
        jurisdiccion: {}, 
        tipoUnidad: {},
        cargar: false,
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/proveedores`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                return {
                    length: data.length,
                    genero: data.map(e => { return parseInt(e.genero) }),
                    edad: data.map(e => { return parseInt(e.edad) }),
                    estado: data.map(e => { return parseInt(e.estado) }), 
                    ocupacion: data.map(e => { return parseInt(e.ocupacion) }),
                    tiempoTrabajando: data.map(e => { return e.tiempoTrabajando }),
                    tipoContrato: data.map(e => { return parseInt(e.tipoContrato) }),
                    jurisdiccion: data.map(e => { return parseInt(e.jurisdiccion) }), 
                    tipoUnidad: data.map(e => { return parseInt(e.tipoUnidad) }) 
                }
            }
        } catch (error) {
            return "error"
        }
    }

    getDataChartGenero = async (data) =>{
        this.setState({
            genero: {
                mujeres: data.filter(e => e === 1).length,
                hombres: data.filter(e => e === 2).length,
                nan: data.filter(e => e !== 1 && e !== 2).length
            }
        });
    }

    getDataChartEdad = async (data) =>{
        this.setState({
            edad: {
                _1_: data.filter(e => e >= 11 && e <= 20).length,
                _2_: data.filter(e => e >= 21 && e <= 30).length,
                _3_: data.filter(e => e >= 31 && e <= 40).length,
                _4_: data.filter(e => e >= 41 && e <= 50).length,
                _5_: data.filter(e => e >= 51).length
           }
        });
    }

    getDataChartEstado = async (data) =>{
        let porcentaje = 100/data.length;
        this.setState({
            estado: {
                morelos: ((data.filter(e => e === 1).length) * porcentaje).toFixed(2),
                yucatan: ((data.filter(e => e === 2).length) * porcentaje).toFixed(2),
           }
        });
    }

    getDataChartOcupacion = async (data) =>{
        this.setState({
            ocupacion: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e === 4).length,
                _5_: data.filter(e => e === 5).length,
            }
        });
    }
    
    // Verificar formato texto
    getDataChartTiempoTrabajando = async (data) =>{
        // console.log(data);
    }
    
    getDataChartTipoContrato = async (data) =>{
        this.setState({
            tipoContrato: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e === 4).length,
                _5_: data.filter(e => e === 5).length,
            }
        });
    }
    
    getDataChartJurisdiccion = async (data) =>{
        this.setState({
            jurisdiccion: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length 
            }
        });
    }
    
    getDataChartTipoUnidad = async (data) =>{
        this.setState({
            tipoUnidad: {
                urbano: data.filter(e => e === 1).length,
                rural: data.filter(e => e === 2).length,
            }
        });
    }

    async componentDidMount(){
        let data = await this.fetchDataAll();

        if (data !== "error")
        {
            await this.getDataChartGenero(data.genero);
            await this.getDataChartEdad(data.edad);
            await this.getDataChartEstado(data.estado);
            await this.getDataChartOcupacion(data.ocupacion);
            await this.getDataChartTipoContrato(data.tipoContrato);
            await this.getDataChartJurisdiccion(data.jurisdiccion);
            await this.getDataChartTiempoTrabajando(data.tiempoTrabajando);
            await this.getDataChartTipoUnidad(data.tipoUnidad);
            this.setState({
                length: data.length,
                cargar: true
            });
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }

        return(
            <Grid fluid style={{margin:0, padding:0}}>
                {/* Porcentajes morelos/yucatan */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="Registros Morelos" md={12} fill={1}>
                            <Circle style={{width: "30%", margin:10}} percent={parseFloat(this.state.estado.morelos)} strokeColor="#ffc107" />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargar ?
                        <Element title="Registros Yucatan" md={12} fill={1}>
                            <Circle style={{width: "30%", margin:10}} percent={parseFloat(this.state.estado.yucatan)} strokeColor="#b3b3ff" />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>
                
                {/* Graficas de barras genero y Ocupacion */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="Gnero" md={11} fill={1}>
                            <ChartBar 
                                width={200} 
                                label="Registros" 
                                data={[this.state.genero.mujeres, this.state.genero.hombres, this.state.genero.nan]} 
                                labels={["Mujeres", "Hombres", "Sin responder"]}
                            />
                        </Element>
                        :<Element md={11}/>
                    }
                    { this.state.cargar ?
                        <Element title="Frecuencia de edades" md={13} fill={1}>
                            <ChartPie
                                width={200} 
                                labels={["11-20 aos", "21-30 aos", "31-40 aos", "41-50 aos", "+50 aos"]} 
                                data={[this.state.edad._1_, this.state.edad._2_, this.state.edad._3_, this.state.edad._4_, this.state.edad._5_]}
                            />
                        </Element>
                        : <Element md={13} />
                    }
                </Row>

                {/* Graficas de edades/tipo contrato */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="Ocupacin" md={11} fill={1}>
                            <ChartHorizontalBar 
                                width={200} 
                                label="Registros"
                                labels={["Mdic@", "Enfermer@", "Nutrilog@", "Auxiliar", "Otro"]} 
                                data={[this.state.ocupacion._1_, this.state.ocupacion._2_, this.state.ocupacion._3_, this.state.ocupacion._4_, this.state.ocupacion._5_]}
                            />
                        </Element>
                        : <Element md={11} />
                    }
                    { this.state.cargar ?
                        <Element title="Tipos de Contrato" md={13} fill={1}>
                            <ChartHorizontalBar 
                                width={200} 
                                label="Registros" 
                                labels={["Base", "Servicio social", "Contrato o eventual", "Confianza", "Otro"]}
                                data={[this.state.tipoContrato._1_, this.state.tipoContrato._2_, this.state.tipoContrato._3_, this.state.tipoContrato._4_, this.state.tipoContrato._5_]} 
                            />
                        </Element>
                        : <Element md={13} />
                    }
                </Row>

                {/*  */}
                <Row className="box-element-container">
                    { this.state.cargar ?
                        <Element title="Tipos de unidad" md={12} fill={1}>
                            <ChartPie
                                width={200}
                                labels={["Urbano", "Rural"]} 
                                data={[this.state.tipoUnidad.urbano, this.state.tipoUnidad.rural]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargar ?
                        <Element title="Jurisdiccin (verificar)" md={12} fill={1}>
                            <ChartBar
                                width={200} 
                                label="Registros"
                                data={[this.state.jurisdiccion._1_, this.state.jurisdiccion._2_, this.state.jurisdiccion._3_]} 
                                labels={["1", "2", "3"]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>
            </Grid>
        );  
    }
}

export default SecGeneral;