import React from 'react';
import Chart from 'react-apexcharts';

function ChartPie(props){
	const data = {
		series: props.data,
		options: {
				chart: {
					width: 380,
					type: 'pie',
				},
				legend: {
					position: 'top'
			},
			labels: props.labels,
			responsive: [{
				breakpoint: 480,
				options: {
					chart: {
						width: 200
					},
					legend: {
						position: 'top'
					}
				}
			}]
		},
	  
	};
	
	return (
		<Chart options={data.options} series={data.series} type="pie" height={`${props.width}px`} />
	);

}

export default ChartPie;