import React from 'react';
import { Link } from "react-router-dom";

function NotFound(){
    return (
        <center>
            <img  style={{width: "40%", padding: "5vw"}} src="https://i.postimg.cc/NFV5FYyw/expediente.png" alt="Not Found"/>
            <p style={{fontSize: "20px", fontWeight: "bold"}}>Volver al <Link to="/nutres/">Inicio</Link></p>
        </center>
    );
}

export default NotFound;