import React, { Component, Fragment } from 'react';
import URL_API from '../../assets/conf';
import { Grid, Row, Progress } from 'rsuite';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import Error from '../elements/error';

const { Circle } = Progress;

class SecGeneral extends Component{
    state ={
        _group_: this.props.group,
        cargar: false,
        data: {},
        error: false,
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/madres/seccion/general/${this.props.group}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                
                return data
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDataAll();

        if (data !== 'error')
        {
            this.setState({
                data: data,
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }
    
    componentDidUpdate = async () =>{
        if (this.props.group !== this.state._group_)
        {
            this.setState({
                cargar: false,
                _group_: this.props.group
            });
            
            await this.handleGetData();
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }
        
        // if (!this.state.cargar){
        //     return <h1>Cargando...</h1>
        // }

        let {data} = this.state;
            
        return (
            <Fragment>
                <Grid fluid style={{margin:0, padding:0}}>
                    {/* Porcentajes morelos/yucatan */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Registros Morelos" md={12} fill={1}>
                                <Circle  style={{width: "30%", margin:10}} percent={data.entidad.morelos} strokeColor="#ffc107" />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Registros Yucatan" md={12} fill={1}>
                                <Circle style={{width: "30%", margin:10}} percent={data.entidad.yucatan} strokeColor="#b3b3ff" />
                            </Element>
                            : <Element md={12} />
                        }
                    </Row>

                    {/* Graficas de grupo, Area unidad y Beneficiario insabi*/}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Grupo" md={8} fill={1}>
                                <ChartPie
                                    width={150}
                                    data={[data.grupo.control, data.grupo.intervencion]} 
                                    labels={["Control", "Intervencin"]}
                                />
                            </Element>
                            :<Element md={8}/>
                        }
                        { this.state.cargar ?
                            <Element title="rea de unidad" md={8} fill={1}>
                                <ChartPie
                                    width={150}
                                    labels={["Rural", "Urbana"]} 
                                    data={[data.areaUnidad.rural, data.areaUnidad.urbana]}
                                />
                            </Element>
                            : <Element md={8} />
                        }
                        { this.state.cargar ?
                            <Element title="Beneficiari@s INSABI" md={8} fill={1}>
                                <ChartPie
                                    width={150}
                                    labels={["Si", "No", "No sabe/Sin resp."]} 
                                    data={[data.bInsabi.si, data.bInsabi.no, data.bInsabi.nan]}
                                />
                            </Element>
                            : <Element md={8} />
                        }
                    </Row>
                    
                    {/* Graficas de Edad y Esta embarazada */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Edad (Cuidadora)" md={13} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["<20 aos", "21-30 aos", "31-40 aos", "41+ aos", "Sin resp."]}
                                    data={[data.edadM._1_, data.edadM._2_, data.edadM._3_, data.edadM._4_, data.edadM.nan]}
                                />
                            </Element>
                            : <Element md={13} />
                        }
                        { this.state.cargar ?
                            <Element title="Actualmente esta embarazada?" md={11} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    data={[data.embarazada.si, data.embarazada.no, data.embarazada.nan]} 
                                    labels={["Si", "No", "No sabe/Sin resp."]}
                                />
                            </Element>
                            :<Element md={11}/>
                        }
                    </Row>

                    {/* Graficas de Edad hijo y sexo hijo */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Sexo hij@" md={11} fill={1}>
                                <ChartBar
                                    label="Registros"
                                    width={200}
                                    data={[data.sexoHijo.hombre, data.sexoHijo.mujer, data.sexoHijo.embarazada, data.sexoHijo.nan]} 
                                    labels={["Hombre", "Mujer", "Embarazada", "Sin resp."]}
                                />
                            </Element>
                            :<Element md={11}/>
                        }
                        { this.state.cargar ?
                            <Element title="Edad (Hij@)" md={13} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["<20 meses", "21-30 meses", "31-40 meses", "40+ meses", "Sin resp."]}
                                    data={[data.edadBebe._1_, data.edadBebe._2_, data.edadBebe._3_, data.edadBebe._4_, data.edadBebe.nan]}
                                />
                            </Element>
                            : <Element md={13} />
                        }
                    </Row>

                </Grid>
            </Fragment>
        );
    }
}

export default SecGeneral;